      </v-btn> -->
    <!-- </v-app-bar> -->

    <v-main>
      <v-container class="full-height pa-0">
        <v-row class="fill-height">
          <v-col cols="2">
            <v-file-input
              label="File input"
              @change="onSelectFileChange"
            ></v-file-input>
          </v-col>

          <v-col cols="8" class="fill-height">
            <v-btn-toggle
              v-model="currentView"
              tile
              color="indigo accent-3"
              group
            >
              <v-btn v-for="item in views" :key="item.display">
                {{ item.display }}
              </v-btn>
            </v-btn-toggle>
          </v-col>

          <v-col cols="2">
            <v-select
              v-model="currentSpectra"
              :items="spectras"
              item-text="name"
              label="Select Spectra"
              persistent-hint
              return-object
              single-line
              @change="onSelectSpectra($event)"
            ></v-select>
          </v-col>

          <v-col cols="12" class="pa-0 ma-0">
            <plotly
              class="graph"
              v-bind="views[currentView].data.data"
              :data="views[currentView].data.data"
              :layout="views[currentView].data.layout"
              @selected="onPlotlySelectPoints"
              @click="onPlotlyClick"
            />
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import heatmaps from "./views/2DHeatmaps";
import scatters from "./views/2DScatters";
import surface from "./views/3DSurface.js";
import interactive from "./views/2DInteractive.js";
import SpectraData from "./misc/SpectraData.js";

export default {
  name: "App",
  data() {
    return {
      views: [heatmaps, scatters, interactive, surface],
      currentView: 0,
      spectras: [
        { id: 0, name: "Channel 0" },
        { id: 1, name: "Channel 1" },
        { id: 2, name: "Channel 2" },
        { id: 3, name: "Channel 3" },
        { id: 4, name: "Channel 4" },
        { id: 5, name: "Channel 5" },
        { id: 6, name: "Channel 6" },
        { id: 7, name: "Channel 7" },
      ],
      currentSpectra: undefined,
      currentFile: undefined,
      dataArray: undefined,
    };
  },
  created: function () {
    // console.log(traces);
    this.currentSpectra = this.spectras[0];
    this.currentView = 0;
  },
  methods: {
    onSelectFileChange(file) {
      // console.log(traces);
      if (file) {
        if (file.size != 1082368) {
          alert(`Corrupted file (size="${file.size}")`);
        } else {
          this.currentFile = file;
          console.log(file);

          let reader = new FileReader();
          reader.readAsArrayBuffer(file);

          var self = this;
          reader.onload = function () {
            // console.log(reader.result);
            self.dataArray = new SpectraData(reader.result);

            // heatmaps
            for (var i = 0; i < 8; i++) {
              heatmaps.data.data[i].z = self.dataArray.data2d[i];
            }
            // scatters
            scatters.data.data = self.dataArray.data1d.map((d) => {
              return {
                type: "scatter",
                y: d,
              };
            });
            // interactive
            // console.log(interactive.data);
            const points = self.dataArray.get2DPoints(
              self.currentSpectra.id,
              0
            );
            // console.log("points:", points);

            // main chart
            interactive.data.data[0]["marker"]["color"] = points.color;
            interactive.data.data[0].x = points.x;
            interactive.data.data[0].y = points.y;
            // data for EdE processing
            const e = [...Array(256).keys()];
            const data = self.dataArray.data2d[self.currentSpectra.id];
            // E chart
            var /*i,*/ j, x, y;
            interactive.data.data[1].x = e;
            interactive.data.data[1].y = [];
            for (i = 0; i < 256; i++) {
              interactive.data.data[1].y.push(
                Math.max.apply(null, data[i])
              );
            }
            //dE chart
            interactive.data.data[2].x = e;
            interactive.data.data[2].y = [];
            for (j = 0; j < 256; j++) {
              (x = j), (y = 0);
              for (i = 1; i < 256; ++i) {
                if (data[i][j] > data[y][x]) {
                  y = i;
                }
              }
              interactive.data.data[2].y.push(data[y][x]);
            }

            // surface
            surface.data.data[0].z =  self.dataArray.data2d[self.currentSpectra.id];
          };

          reader.onerror = function () {
            console.error(reader.error);
          };
        }
      } else {
        //TODO clear data
        // heatmaps
        for (let i = 0; i < 8; i++) {
          heatmaps.data.data[i].z = [];
        }
        // scatters
        scatters.data.data = [];
        // interactive
        for (let i = 0; i < 3; i++) {
            interactive.data.data[i]["marker"]["color"] = [];
            interactive.data.data[i].x = [];
            interactive.data.data[i].y = [];
        }
        // surface
        surface.data.data[0].z = [];
      }
    },
    onSelectSpectra(event) {
      // console.log("view id:", this.currentView);
      console.log("onSelectSpectra", event.id, event.name);
      // console.log(this.currentSpectra);
      // interactive
      const points = this.dataArray
        ? this.dataArray.get2DPoints(this.currentSpectra.id, 0)
        : {
            color: [],
            x: [],
            y: [],
          };
      // console.log("points:", points);
      // main chart
      interactive.data.data[0]["marker"]["color"] = points.color;
      interactive.data.data[0].x = points.x;
      interactive.data.data[0].y = points.y;
      // data for EdE processing
      const e = [...Array(256).keys()];
      if (this.dataArray) {
        const data = this.dataArray.data2d[this.currentSpectra.id];
        // E chart
        var i, j, x, y;
        interactive.data.data[1].x = e;
        interactive.data.data[1].y = [];
        for (i = 0; i < 256; i++) {
          interactive.data.data[1].y.push(Math.max.apply(null, data[i]));
        }
        //dE chart
        interactive.data.data[2].x = e;
        interactive.data.data[2].y = [];
        for (j = 0; j < 256; j++) {
          (x = j), (y = 0);
          for (i = 1; i < 256; ++i) {
            if (data[i][j] > data[y][x]) {
              y = i;
            }
          }
          interactive.data.data[2].y.push(data[y][x]);
        }
      } else {
        interactive.data.data[1].x = e;
        interactive.data.data[1].y = [];
        interactive.data.data[2].x = e;
        interactive.data.data[2].y = [];
      }

      // surface
      surface.data.data[0].z = this.dataArray ? this.dataArray.data2d[this.currentSpectra.id] : [];
    },
    onPlotlySelectPoints(points, id) {
      console.log("onPlotlySelectPoints", points, id);

      // var p = points.points[0];
      // console.log(p.x, p.y, p["marker.color"]);

      const e = [...Array(256).keys()];
      let scale_e = Array.from(Array(256), () => 0);
      let scale_de = Array.from(Array(256), () => 0);

      var index;
      for (index = 0; index < points.points.length; index++) {
        scale_e[points.points[index].x] = Math.max(
          scale_e[points.points[index].x],
          points.points[index]["marker.color"]
        );
        scale_de[points.points[index].y] = Math.max(
          scale_e[points.points[index].y],
          points.points[index]["marker.color"]
        );
      }

      // E chart
      interactive.data.data[1].x = e;
      interactive.data.data[1].y = scale_e;

      //dE chart
      interactive.data.data[2].x = e;
      interactive.data.data[2].y = scale_de;
    },
    onPlotlyClick(data) {
      if (this.currentView === 0) {
        console.log("onPlotlyClick", data.points[0].curveNumber);
        this.currentView = 2; // interactive
        this.currentSpectra = this.spectras[data.points[0].curveNumber];
        this.onSelectSpectra(this.currentSpectra);
      } else {
        console.log("Skip click handling");
      }
    },
  },
};
</script>

<style>
.graph {
  height: 80vh;
}
</style>
