class SpectraData {
    constructor(data) {
        this.description = new TextDecoder().decode(
            new Uint8Array(data, 0, 1024 / 2)
        );
        // 1D
        this.data1d = Array.from({
            length: 16,
        }).map((_, i) => new Uint16Array(data, 1024 + i * 1024 * 2, 1024));
        // 2D
        const offset2d = 1024 + 1024 * 2 * 16;
        this.data2d = Array.from({
            length: 8,
        }).map((_, i) =>
            Array.from({
                length: 256,
            }).map(
                (_, j) =>
                new Uint16Array(data, offset2d + i * 256 * 256 * 2 + 256 * j * 2, 256)
            )
        );
        // console.log("description:", this.description.trim());
        // console.log("data1d:", this.data1d);
        // console.log("data2d:", this.data2d);
    }

    get2DPoints(idx, lowerBound = 0, upperBound = Number.MAX_VALUE) {
        var i, j;
        var points = {
            color: [],
            x: [],
            y: [],
        };
        const data = this.data2d[idx];
        for (i = 0; i < 256; i++) {
            for (j = 0; j < 256; ++j) {
                if (data[i][j] > lowerBound && data[i][j] < upperBound) {
                    points.color.push(data[i][j]);
                    points.y.push(i);
                    points.x.push(j);
                }
            }
        }
        return points;
    }
}

export default SpectraData;